* {
  box-sizing: border-box;
}

html,
body {
  font-family: Arial, sans-serif;
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
}
* {
  box-sizing: border-box;
}

html, 
body {
  width: 100vw;
  height: 100vh;
}

body {
  padding: 0;
  margin: 0;
  font-family: sans-serif;
}

.App {
  height: 100vh;
  width: 100vw;
  overflow-y: hidden;
}

main {
  width: 100%;
  position: absolute;
  top: 56px;
  right: 0;
  height: calc(100vh - 56px);
}

h1, h2, h3 {
  margin: 0px;
}

.info-window{
  width: 207px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.info-window img {
  border-radius: 10%;
  width: 100%;
}

.info-window p {
  margin: 4px 0;
}

.opening {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-top: 16px;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  justify-content: space-between;
}

.opening-title {
  width: 100%;
}

.opening-days {
  width: 40%;
}

.opening-hours {
  width: 60%;
}

/*style the infoWindow close button*/  
button.gm-ui-hover-effect img {
  height: 20px !important;
  width: 20px !important;
}

/*style the infoWindow box*/  
.gm-style div div:nth-child(3) div:nth-child(4) div{
  border-radius: 5% !important;
}

@media screen and (min-width: 1025px) {
  main {
    width: 80%;
  }
}

@media screen and (max-width: 359px) {
  .info-window{
    width: 167px;
  }
}
.map {
	width: 100%;
	height: 100%;
}

.map-error {
	background-color: rgba(0,0,0,0.15);
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	color: #A30000;
}

.map-error p {
	margin: 0;
	font-size: 1.5em;
	padding-top: 16px;
}

.map-error-icon {
	font-size: 10em;
}
.toolbar {
  position: absolute;
  top:0;
  right: 0;
  width: 100vw;
  background: #A41E4D;
  height: 56px;
}

.toolbar-navigation {
  display: flex;
  height: 100%;
  align-items: center;
}

.toolbar-menu,
.toolbar-search-icon {
  background-color: #A41E4D;
  border: none;
  color: #FAFAFA;
  padding: 12px 16px;
  font-size: 24px;
  cursor: pointer;
}

.toolbar-search-icon.close {
  display: none;
}

.toolbar-logo.close {
  width: 0;
}

.toolbar-logo h1{
  font-size: 1.5em;
  font-weight: 300;
  color: #FAFAFA;
  overflow: hidden;
  white-space: nowrap;
}

.toolbar-search-input.open {
  padding: 6px;
  font-size: 1.2em;
  width: calc(100% - 54px);
  margin: 0;
  border-width: 0 0 1px 0;
  margin-right: 16px;
  color:#FAFAFA;
  opacity: 0.9;
  transition: width 0.4s linear;

}

.toolbar-search-input {
  width: 0;
  border-width: 0;
  background-color: #A41E4D;
}

.toolbar-search-input::-webkit-input-placeholder {
  color: #FAFAFA;
  opacity: 0.7; 
}

.toolbar-search-input::-ms-input-placeholder {
  color: #FAFAFA;
  opacity: 0.7; 
}

.toolbar-search-input::placeholder {
  color: #FAFAFA;
  opacity: 0.7; 
}

.toolbar-search-input:focus {
  outline: none;
}

.spacer {
  flex: 1 1;
}

@media screen and (min-width: 1025px) {
  .toolbar {
    width: 80%;
  }
  .toolbar-menu {
    display: none;
  }
  .toolbar-logo {
    margin-left: 16px;
  }

  .toolbar-search-input.open {
    width: 40%;
  }
}

@media screen and (min-width: 768px) {
  .toolbar-logo.close {
    display: block;
  }
  .toolbar-logo.close {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }

}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .toolbar-search-input.open {
    width: 50%;
  }

}
.toast {
  align-items: center;
  border-radius: 25px;
  bottom: 0px;
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  min-height: 50px;
  max-height: 50px;
  justify-content: space-between;
  left: 50%;
  padding: 0 20px;
  background-color: #F38211;
  position: fixed;
  -webkit-transform: translateX(-50%) translateY(150%);
          transform: translateX(-50%) translateY(150%);
  transition: -webkit-transform .35s ease;
  transition: transform .35s ease;
  transition: transform .35s ease, -webkit-transform .35s ease;
}

.toast.visible {
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}

.toast p {
  margin-left: 8px;
}
.side-drawer {
  height: 100vh;
  background-color: #FAFAFA;
  box-shadow: 1px 0px 7px rgba(0,0,0,0.5) ;
  position: absolute;
  top: 0;
  left: 0;
  width: 60%;
  max-width: 250px;
  z-index: 200;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  overflow-y: auto;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side-drawer.open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.options-button {
  width: 100%;
  margin: 0;
  background-color: #FAFAFA;
  border-style: solid;
  border-color: transparent;
  border-width: 3px;
  color: #A41E4D;
  padding: 16px 16px;
  text-align: left;
  text-decoration: none;
  display: inline-block;
  font-size: 1em;
  cursor: pointer;
}

.options-button:hover,
.options-button:focus,
.options-button:active {
  background-color: #D61A8E;
  color: #FAFAFA;
}

.options-button.clicked:hover,
.options-button.clicked:focus,
.options-button.clicked:active{
  background-color: #D61A8E;
  color: #FAFAFA;
}

.options-button.clicked {
  background-color: #F7BBD9;
}

.side-drawer-icons {
  margin-right: 8px;
}

.info {
  margin-right: 16px;
}

.foursquare {
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  height: 56px;
  top: calc(100% - 50px);
  padding: 16px;
  text-align: right;
}

@media screen and (min-width: 1025px) {
  .side-drawer {
    max-width: 20%;
    -webkit-transform: translateX(0);
            transform: translateX(0);
    transition: -webkit-transform 0.0s;
    transition: transform 0.0s;
    transition: transform 0.0s, -webkit-transform 0.0s;
  }
}
.backdrop {
	position: absolute;
	width: 100%;
	height: calc(100vh - 56px);
	top: 56px;
	left: 0;
	background-color: rgba(0,0,0,0.3);
	z-index: 100;
}

.search-list {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  max-width: 60%;
  z-index: 200;
  position: fixed;
  top:56px;
  right: 0px;
  height: 100%;
  overflow-y: auto;
  -webkit-transform: translateX(100%);
          transform: translateX(100%); 
  transition: -webkit-transform 0.3s linear; 
  transition: transform 0.3s linear; 
  transition: transform 0.3s linear, -webkit-transform 0.3s linear;
}

.search-list.open {
  -webkit-transform: translateX(0);
          transform: translateX(0); 
}

.search-list ul{
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: #FAFAFA;
}

.search-list li {
  color: #A81E4F;
  padding: 16px;
  font-size: 1em;
  width: 100%;
}

.search-list li:hover,
.search-list li:active {
  background-color: #D61A8E;
  color: #FAFAFA;
  cursor: pointer;
}

@media screen and (min-width: 1025px) {
  .search-list {
  }
}

